import pieChart from "./components/PieChart"; // eslint-disable-line

import { order, orderdetail, ordercharts, ordertendency } from "@/api/order"; // eslint-disable-line

import LineChart from "@/components/LineChart/LineChart"; // eslint-disable-line

import { getDay, get } from "@/utils/dateFormat"; // eslint-disable-line

var moment = require('moment');

var str = ""; // eslint-disable-line

var getScen = function getScen(res) {
  str = res.scen;

  if (res.children) {
    res.children.forEach(function (item) {
      str += ',' + item.scen;

      if (item.children) {
        item.children.forEach(function (one) {
          str += ',' + one.scen;
        });
      }
    });
  }

  return str;
}; // eslint-disable-line


export default {
  name: 'Tab',
  components: {
    pieChart: pieChart,
    LineChart: LineChart
  },
  data: function data() {
    return {
      pieData: '',
      tableData: [],
      total: '',
      current: 1,
      dateRange: [],
      showPie: false,
      showHeader: false,
      dialogVisible: false,
      lineChartData: {
        tit: '',
        actualData: [],
        xdate: []
      },
      chartData: [],
      btnList: [{
        tit: '访客数',
        type: 'visitor_num'
      }, {
        tit: '跳失率',
        type: 'jumpr'
      }, {
        tit: '销售额',
        type: 'pay_money'
      }, {
        tit: '销售量',
        type: 'pay_num'
      }, {
        tit: '订单数',
        type: 'order_num'
      }],
      oby: 'visitor_num',
      obyt: 2
    };
  },
  created: function created() {
    // this.getOrders();
    this.getordercharts();
    this.getOrderDetails();
  },
  methods: {
    // rowStyle(row) {
    //   console.log(row.row);
    //   if (row.row.children) {
    //     console.log(row);
    //     return "";
    //   } else {
    //     return "children";
    //     console.log("========================8898");
    //   }
    // },
    renderHeader: function renderHeader(h, _ref) {
      var column = _ref.column;
      var tit = column.label;
      var tip = '';

      switch (tit) {
        case '访客数':
          tip = '统计所选时间段内，访问小程序商城每个界面的所有访客去重后的数量累计和（不区分移动/PC）';
          break;

        case '访客深度':
          tip = '统计所选时间段内，访问小程序商城界面持续打开数量的人均访问深度';
          break;

        case '跳失率':
          tip = '1-（点击人数/访客数）*100%';
          break;

        case '收藏小程序':
          tip = '小程序收藏的业务单据数据累计和';
          break;

        case '收藏商品':
          tip = '商品收藏的数据累计和';
          break;

        case '销售额':
          tip = '所有全款支付的订单支付金额累计和';
          break;

        case '销售量':
          tip = '已支付完成的所有订单数量';
          break;

        case '订单数':
          tip = '所有已提交的订单数量';
          break;

        case '支付转化率':
          tip = '所有全款支付的订单买家去重/访客数';
          break;
      }

      return h("el-tooltip", {
        "class": 'item',
        "attrs": {
          "effect": 'light',
          "content": tip,
          "placement": 'top'
        }
      }, [h("span", [column.label])]);
    },
    sortChange: function sortChange(e) {
      console.log(e);
      this.oby = e.prop;
      this.obyt = e.order === 'ascending' ? 1 : 2;
      this.getOrderDetails();
    },
    reset: function reset() {
      this.dateRange = [];
      this.getOrderDetails();
    },
    getTend: function getTend(row) {
      var _this = this;

      this.dialogVisible = true;
      var param = {
        scen: getScen(row)
      };

      if (this.dateRange.length > 1) {
        param.start_time = parseInt(this.dateRange[0] / 1000);
        param.end_time = parseInt(this.dateRange[1] / 1000);
        var st = moment(this.dateRange[0]).format('YYYY-MM-DD');
        var et = moment(this.dateRange[1]).format('YYYY-MM-DD');
        this.lineChartData.xdate = get(st, et);
      } else {
        var temp = [];

        for (var i = -6; i < 1; i++) {
          temp.push(getDay(i));
        }

        this.lineChartData.xdate = temp;
      }

      ordertendency(param).then(function (res) {
        console.log(res);
        _this.chartData = res.data;
        _this.lineChartData.tit = "访客数"; // eslint-disable-line

        _this.lineChartData.actualData = res.data.visitor_num;
      });
    },
    choose: function choose(item) {
      this.lineChartData.actualData = this.chartData[item.type];
      this.lineChartData.tit = item.tit;
    },
    toDetail: function toDetail(res) {
      var param = this.dateRange.length ? {
        scen: res,
        start_time: parseInt(this.dateRange[0] / 1000),
        end_time: parseInt(this.dateRange[1] / 1000)
      } : {
        scen: res
      };
      this.$router.push({
        name: 'treasure',
        query: param
      });
    },
    getordercharts: function getordercharts() {
      var _this2 = this;

      ordercharts().then(function (res) {
        _this2.pieData = res.data;
        _this2.showPie = true;
      });
    },
    getOrders: function getOrders() {
      var _this3 = this;

      order().then(function (response) {
        console.log(response, '================');
        _this3.pieData = response;
        console.log(_this3.pieData, '==============');
        _this3.showPie = true;
      }).catch(function (err) {
        console.log(err);
      });
    },
    showCreatedTimes: function showCreatedTimes() {
      this.createdTimes = this.createdTimes + 1;
    },
    getOrderDetails: function getOrderDetails() {
      var _this4 = this;

      var param = {};
      param = {
        oby: this.oby,
        obyt: this.obyt
      };

      if (this.dateRange.length > 1) {
        param.start_time = parseInt(this.dateRange[0] / 1000);
        param.end_time = +parseInt(this.dateRange[1] / 1000) + 86399;
      }

      console.log(param, '============');
      orderdetail(param).then(function (response) {
        console.log(response.data, '================');
        _this4.tableData = response.data;

        _this4.tableData.forEach(function (item) {
          item.jumpr = Number(item.jumpr).toFixed(2);
        }); // this.total = response.count;

      }).catch(function (err) {
        console.log(err);
      });
    }
  }
};