import request from '@/utils/request';
export function order(query) {
  return request({
    url: '/order/1',
    method: 'get',
    params: query
  });
}
export function orderdetail(data) {
  return request({
    url: '/app/business/ordertable',
    method: 'post',
    data: data
  });
}
export function ordercharts() {
  return request({
    url: '/app/business/ordercharts',
    method: 'post'
  });
}
export function ordertendency(data) {
  return request({
    url: '/app/business/ordertendency',
    method: 'post',
    data: data
  });
}